@import './Constants.css';

.tab-list {
    border-bottom: 1px solid #ccc;
    padding-left: 0;
    background-color: var(--header-background-color);
    margin-top: 0;
}

.tab-list-item {
    background-color: unset;
    color: var(--other-btn-color);
    font-weight: bold;
    display: inline-block;
    list-style: none;
    margin-bottom: -1px;
    margin-top: -1px;
    margin-left: -1px;
    padding: 0.5rem 0.75rem;
    border: solid white;    /*White with width to reserve space for active gray border*/
    border-width: 1px 0px 0 1px;
}

.tab-list-active {
    background-color: white;
    border-color: #cccccc;
}