@import './Constants.css';

.lists-container button {
    background-color: #fcfcfc;
    border: solid 1px #d3d3d3;
    border-radius: 4px;
    color: #3b3b3b;
    cursor: pointer;
    height: 28px;
    width: 56px;
}

.lists-dropdown {
    position: relative;
    display: inline-block;
}

.lists-dropdown img {
    height: 15px;
    width: 17px;
}

/*Styling for Home Page Lists*/

#home-lists-container {
    display: block;
    overflow: auto;
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.08);
    border-radius: 4px;
    font-size: 14px;
    font-weight: normal;
    margin-top: 30px;
}

#home-lists-container a:not(:last-child) {
    border-bottom: rgba(119, 119, 119, 0.2) solid 1px;
}

#home-lists-container a {
    display: flex;
    justify-content: space-between;
}

#list-name {
    font-weight: bold;
}

#list-isUnowned {
    color: var(--delete-btn-color);
    font-weight: bold;
}

#list-isShared {
    color: var(--other-btn-color);
    font-weight: bold;
}

/* List Display Page Styling*/

.lists-dropdown button {
    background-color: #fcfcfc;
    border: none;
    border-radius: 4px;
    color: #3b3b3b;
    cursor: pointer;
    height: 28px;
}

.lists-dropdown-content {
    display: block;
    position: absolute;
    min-width: 140px;
    overflow: auto;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    border-radius: 4px;
    z-index: 1;
    font-size: 14px;
    font-weight: normal;
}

.list-unselected {
    background-color: white;
    color: black;
    padding: 8px;
    text-decoration: none;
    display: block;
    cursor: pointer;
}

.list-selected {
    background-color: #e5e5e5 !important;
}

.lists-dropdown-content a:not(:last-child) {
    border-bottom: rgba(119, 119, 119, 0.2) solid 1px;
}

#add-new-list {
    height: 28px;
    text-align: center;
    margin-top: 16px;
    width: 50%;
    display: flex;
}

#add-list-form {
    display: flex;
}

#input-list-field {
    border: none;
    border-radius: 6px 0 0 6px;
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.08);
    padding-left: 8px;
    width: 180px;
    margin-left: auto;
}

#add-list-btn-active {
    width: 30px;
}

#add-list-btn-disabled {
    width: 30px;
}

@media screen and (min-width: 640px) {
    #add-new-list {
        height: 38px;
    }
    #input-list-field {
        width: 400px;
    }
    #add-list-btn-active {
        width: 38px;
    }
    #add-list-btn-disabled {
        width: 38px;
    }
}

.delete-list-btn {
    background-color: #fcfcfc;
    border: none;
    border-radius: 4px;
    color: #3b3b3b;
    cursor: pointer;
    height: 28px;
    margin-left: 4px;
    padding-top: 6px;
}

.delete-list-btn img {
    height: 15px;
    width: 15px;
}